/* src/components/SobreMi.css */
#sobre-mi {
    background: linear-gradient(135deg, #f8f8f8, #f0f0f0);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 50px 20px;
    box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.1);
    position: relative;
    overflow: hidden;
  }
  
  #sobre-mi .container {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  #sobre-mi h2 {
    font-size: 3rem;
    font-family: 'Montserrat', sans-serif;
    margin-bottom: 40px;
    color: #333;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
  }
  
  .about-content, .philosophy-content, .stats-content, .quote-content {
    margin-bottom: 40px;
  }
  
  .about-content {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: left;
    margin-bottom: 60px;
    flex-wrap: wrap; /* Ajuste para dispositivos móviles */
  }
  
  .about-text {
    flex: 1;
    padding-right: 20px;
    animation: fadeIn 2s ease-in-out;
    max-width: 500px;
    font-size: 1.25rem; /* Aumentar el tamaño del texto */
  }
  
  .about-image {
    flex: 1;
    padding-left: 20px;
    display: flex;
    justify-content: center; /* Centrar la imagen */
    align-items: flex-end; /* Alinear la parte inferior de la imagen con el texto */
  }
  
  .about-image img {
    width: 100%;
    max-width: 400px; /* Limita el ancho máximo de la imagen */
    height: auto;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    animation: parallax 5s infinite;
  }
  
  .philosophy-content, .stats-content, .quote-content {
    animation: fadeIn 2s ease-in-out;
  }
  
  .stats-content {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  
  .stat-item {
    flex: 1 1 200px;
    margin: 10px;
    padding: 20px;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .stat-item h4 {
    font-size: 1.5rem;
    margin-bottom: 10px;
    color: #333;
  }
  
  .stat-item p {
    font-size: 1rem;
    color: #555;
  }
  
  .stat-icon {
    font-size: 2rem;
    margin-bottom: 10px;
    color: #333;
  }
  
  .quote-content blockquote {
    font-size: 1.5rem;
    font-style: italic;
    color: #333;
    padding: 20px;
    border-left: 5px solid #333;
    background: rgba(255, 255, 255, 0.8);
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  
  @keyframes parallax {
    0% {
      transform: translateY(0px);
    }
    50% {
      transform: translateY(-15px);
    }
    100% {
      transform: translateY(0px);
    }
  }
  