/* src/components/Portfolio.css */
#portfolio {
  background: linear-gradient(135deg, #f0f0f0, #dcdcdc);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 50px 20px;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.1);
}

#portfolio h2 {
  text-align: center;
  font-size: 3rem;
  font-family: 'Montserrat', sans-serif;
  margin-bottom: 30px;
  color: #333;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

.portfolio-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.portfolio-description {
  font-size: 1.2rem;
  color: #555;
  font-family: 'Roboto', sans-serif;
  margin-bottom: 30px;
}

.cta {
  background: #333;
  color: #fff;
  padding: 30px 20px;
  margin-top: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.cta h3 {
  font-size: 2rem;
  margin-bottom: 20px;
}

.cta button {
  background: #fff;
  color: #333;
  border: none;
  padding: 15px 30px;
  font-size: 1.2rem;
  cursor: pointer;
  transition: background 0.3s, color 0.3s, transform 0.3s;
  border-radius: 5px;
}

.cta button:hover {
  background: #9488f3;
  color: #fff;
  transform: scale(1.05);
}

.image-gallery-thumbnail img {
  border-radius: 5px;
}

.image-gallery-thumbnail.active img,
.image-gallery-thumbnail:hover img {
  border: 2px solid #333;
  transform: scale(1.1);
  transition: transform 0.3s ease;
}
